/**
 * @function getFormattedDate
 * @description  –£—Ç–∏–ª–∏—Ç–∞ –ø—Ä–µ–æ–±—Ä–∞–∑–æ–≤–∞–Ω–∏—è –¥–∞—Ç—ã.<br>
 * –ü—Ä–∏–Ω–∏–º–∞–µ—Ç –¥–∞—Ç—É –≤ —Ñ–æ—Ä–º–∞—Ç–µ UTC, –ø—Ä–µ–æ–±—Ä–∞–∑—É–µ—Ç –≤ —Å—Ç—Ä–æ–∫—É —Ç–∏–ø–∞ "2 —è–Ω–≤–∞—Ä—è, 2020"
 * @param {String} dateUTC -  —Å—Ç—Ä–æ–∫–∞, –¥–∞—Ç–∞ —Å—Ç–∞—Ç—å–∏
 * @returns {String}
 * @since v.1.1.0
 */
export const getFormattedDate = (dateUTC) => {
  const months = [
    '—è–Ω–≤–∞—Ä—è',
    '—Ñ–µ–≤—Ä–∞–ª—è',
    '–º–∞—Ä—Ç–∞',
    '–∞–ø—Ä–µ–ª—è',
    '–º–∞—è',
    '–∏—é–Ω—è',
    '–∏—é–ª—è',
    '–∞–≤–≥—É—Å—Ç–∞',
    '—Å–µ–Ω—Ç—è–±—Ä—è',
    '–æ–∫—Ç—è–±—Ä—è',
    '–Ω–æ—è–±—Ä—è',
    '–¥–µ–∫–∞–±—Ä—è',
  ];

  const monthNumber = new Date(dateUTC).getMonth();
  const letterMonth = months[monthNumber];
  const numericDay = new Date(dateUTC).getDate();
  const numericYear = new Date(dateUTC).getFullYear();

  return `${numericDay} ${letterMonth}, ${numericYear}`;
};
