import * as PATH_TO from './endpoints';
import { NEWS_API_URL, PAGE_SIZE, API_KEY, DATE_FROM, DATE_TO } from '../configs';

/**
 * @module NewsApi
 * @description –ú–æ–¥—É–ª—å –≤–∑–∞–∏–º–æ–¥–µ–π—Å—Ç–≤–∏—è —Å –Ω–æ–≤–æ—Å—Ç–Ω—ã–º —Å–µ—Ä–≤–µ—Ä–æ–º.
 * @since v.1.1.0
 */

/**
 * @method getArticlesFromNewsApi
 * @description –ú–µ—Ç–æ–¥ –ø–æ–ª—É—á–µ–Ω–∏—è —Å—Ç–∞—Ç–µ–π –ø–æ –∫–ª—é—á–µ–≤–æ–º—É —Å–ª–æ–≤—É (–∑–∞–ø—Ä–æ—Å—É) –æ—Ç –Ω–æ–≤–æ—Å—Ç–Ω–æ–≥–æ —Å–µ—Ä–≤–µ—Ä–∞.
 * –ü–æ –∫–ª—é—á–µ–≤–æ–º—É —Å–ª–æ–≤—É –∏—â–µ—Ç —Å—Ç–∞—Ç—å–∏ –Ω–∞ –Ω–æ–≤–æ—Å—Ç–Ω–æ–º —Å–µ—Ä–≤–µ—Ä–µ, –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç:
 * -  –º–∞—Å—Å–∏–≤ —Å –¥–∞–Ω–Ω—ã–º–∏ –Ω–æ–≤–æ—Å—Ç–µ–π,
 * - –ø—É—Å—Ç–æ–π –º–∞—Å—Å–∏–≤, –µ—Å–ª–∏ —Å—Ç–∞—Ç—å–∏ –Ω–µ –Ω–∞–π–¥–µ–Ω—ã,
 * - –æ—à–∏–±–∫—É, –µ—Å–ª–∏ –∑–∞–ø—Ä–æ—Å –∑–∞–≤–µ—Ä—à–∏–ª—Å—è –Ω–µ—É–¥–∞—á–µ–π
 * @param {String} userQuery - –∫–ª—é—á–µ–≤–æ–µ —Å–ª–æ–≤–æ (–ø–æ–∏—Å–∫–æ–≤—ã–π –∑–∞–ø—Ä–æ—Å)
 * @returns {Array} –º–∞—Å—Å–∏–≤ —Å –¥–∞–Ω–Ω—ã–º–∏ –Ω–∞–π–¥–µ–Ω–Ω—ã—Ö –Ω–æ–≤–æ—Å—Ç–µ–π
 * @since v.1.1.0
 */
export const getArticlesFromNewsApi = (userQuery) => {
  return fetch(
    `${NEWS_API_URL}${PATH_TO.EVERYTHING}?q=${userQuery}&from=${DATE_FROM}&to=${DATE_TO}&pageSize=${PAGE_SIZE}&apiKey=${API_KEY}`,
    {
      method: 'GET',
    },
  ).then((res) => {
    if (res.status) {
      return res.json();
    }
    return Promise.reject(`–°—Ç–∞—Ç—É—Å –æ—Ç–≤–µ—Ç–∞: ${res.status}`);
  });
};
