import React from 'react';
import { Route, Redirect } from 'react-router-dom';
import { MAIN } from '../../utils/routesMap';
import PropTypes from 'prop-types';

/**
 * @module ProtectedRoute
 * @description HOC-–∫–æ–º–ø–æ–Ω–µ–Ω—Ç<br>
 * –ó–∞—â–∏—â–∞–µ—Ç —Ä–æ—É—Ç saved-news.
 * @param {Object} props - –ø—Ä–æ–ø—Å—ã - –∑–∞—â–∏—â–∞–µ–º—ã–π –∫–æ–º–ø–æ–Ω–µ–Ω—Ç –∏ –µ–≥–æ –ø—Ä–æ–ø—Å—ã
 * @returns {JSX}
 * @since v.1.1.0
 */
const ProtectedRoute = ({ component: Component, ...props }) => {
  return (
    <Route>
      {() =>
        props.isLoggedIn ? <Component {...props} /> : <Redirect to={{ pathname: MAIN, state: 1 }} />
      }
    </Route>
  );
};

ProtectedRoute.propTypes = {
  component: PropTypes.elementType,
  props: PropTypes.objectOf(PropTypes.any),
};

export default ProtectedRoute;
