import { useCallback, useState } from 'react';

/**
 * @module useFormWithValidation
 * @description –ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å—Å–∫–∏–π —Ö—É–∫.<br>
 * –ó–∞–ø—É—Å–∫–∞–µ—Ç –≤–∞–ª–∏–¥–∞—Ü–∏—é —Ñ–æ—Ä–º—ã, —Å –∫–æ—Ç–æ—Ä–æ–π –∏—Å–ø–æ–ª—å–∑—É–µ—Ç—Å—è. –î–ª—è –∑–∞–ø—É—Å–∫–∞ –≤–∞–ª–∏–¥–∞—Ü–∏–∏, —Ö—É–∫
 *  –∏–º–ø–æ—Ä—Ç–∏—Ä—É–µ—Ç—Å—è –≤ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç —Å —Ñ–æ—Ä–º–æ–π –∏ –∑–∞–ø—É—Å–∫–∞–µ—Ç—Å—è. –•—É–∫ –≤—ã–ø–æ–ª–Ω—è–µ—Ç –≤–∞–ª–∏–¥–∞—Ü–∏—é –∏ –≤—ã–≤–æ–¥ –æ—à–∏–±–æ–∫
 *  –∏—Å–ø–æ–ª—å–∑—É—è –±—Ä–∞—É–∑–µ—Ä–Ω—É—é –≤–∞–ª–∏–¥–∞—Ü–∏—é, —Ç.–µ. —Å–≤–æ–π—Å—Ç–≤–æ validity –æ–±—ä–µ–∫—Ç–∞ ValidityState.<br>
 * –û—Å–æ–±–µ–Ω–Ω—Å—Ç–∏ —Ö—É–∫–∞:<br>
 * - –∏—Å–ø–æ–ª—å–∑—É—é—Ç—Å—è —É–ø—Ä–∞–≤–ª—è–µ–º—ã–µ –∏–Ω–ø—É—Ç—ã;<br>
 * - –∫–∞–∂–¥—ã–π –∏–Ω–ø—É—Ç –≤–∞–ª–∏–¥–∏—Ä—É–µ—Ç—Å—è –æ—Ç–¥–µ–ª—å–Ω–æ, —á—Ç–æ –ø–æ–∑–≤–æ–ª—è–µ—Ç —Å—Ç–∏–ª–∏–∑–æ–≤–∞—Ç—å –∫–∞–∂–¥—ã–π –∏–Ω–ø—É—Ç –≤ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏
 *  –æ—Ç –µ–≥–æ —Å–æ—Å—Ç–æ—è–Ω–∏—è<br>
 * - –ø—Ä–æ–≤–µ—Ä—è–µ—Ç—Å—è –≤–∞–ª–∏–¥–Ω–æ—Å—Ç—å –≤—Å–µ–π —Ñ–æ—Ä–º—ã –≤ —Ü–µ–ª–æ–º, –µ—Å–ª–∏ –Ω–µ –≤–∞–ª–∏–¥–µ–Ω —Ö–æ—Ç—è –±—ã –æ–¥–∏–Ω –∏–Ω–ø—É—Ç - –≤—Å—è —Ñ–æ—Ä–º–∞ –Ω–µ –≤–∞–ª–∏–¥–Ω–∞<br>
 * - –∑–∞–ø–æ–ª–Ω—è–µ–º—ã–π –∏–Ω–ø—É—Ç –ø–æ–ª—É—á–∞–µ—Ç —Å–æ—Å—Ç–æ—è–Ω–∏–µ "–≤—ã–±—Ä–∞–Ω–Ω—ã–π", —á—Ç–æ –ø–æ–∑–≤–æ–ª—è–µ—Ç –æ—Ç–¥–µ–ª—å–Ω–æ —Å—Ç–∏–ª–∏–∑–æ–≤–∞—Ç—å –∏–Ω–ø—É—Ç, –≤ –∫–æ—Ç–æ—Ä–æ–º
 *  –ø—Ä–æ–∏—Å—Ö–æ–¥–∏—Ç –∑–∞–ø–æ–ª–Ω–µ–Ω–∏–µ –ø–æ–ª—è<br>
 * –í–æ–∑–≤—Ä–∞—â–∞–µ—Ç –æ–±—ä–µ–∫—Ç —Å–æ —Å—Ç–µ–π—Ç–∞–º–∏ –∏ –º–µ—Ç–æ–¥–∞–º–∏.
 * @returns {Object}  { values, errors, isValid, handleInputChange, setValues, resetForm }
 * @since v.2.0.6
 * @public
 */
export function useFormWithValidation() {
  const [values, setValues] = useState({});
  const [errors, setErrors] = useState({});
  const [isFormValid, setIsFormValid] = useState(false);
  const [isInputChecked, setIsInputChecked] = useState({});
  const [isInputValid, setIsInputValid] = useState({});
  const [formError, setFormError] = useState('');

  /**
   * @method
   * @name handleInputChange
   * @argument {Event} event - —Å–æ–±—ã—Ç–∏–µ
   * @description –û–±—Ä–∞–±–æ—Ç—á–∏–∫ –∏–∑–º–µ–Ω–µ–Ω–∏—è –ø–æ–ª–µ–π –∏–Ω–ø—É—Ç–æ–≤.<br> –ü—Ä–∏ –∫–∞–∂–¥–æ–º –≤–≤–æ–¥–µ –≤ –ø–æ–ª–µ –∏–Ω–ø—É—Ç–∞ –≤–≤–µ–¥–µ–Ω–Ω–æ–µ –∑–Ω–∞—á–µ–Ω–∏–µ
   *  –∏ —Ä–µ–∑—É–ª—å—Ç–∞—Ç—ã –≤–∞–ª–∏–¥–∞—Ü–∏–∏ –≤–≤–µ–¥–µ–Ω–Ω—ã—Ö –∑–Ω–∞—á–µ–Ω–∏–π –∏ —Ñ–æ—Ä–º—ã –≤ —Ü–µ–ª–æ–º —Å–æ—Ö—Ä–∞–Ω—è—é—Ç—Å—è –≤ —Å–æ–æ—Ç–≤–µ—Ç—Å—Ç–≤—É—é—â–∏–µ —Å—Ç–µ–π—Ç—ã.
   * @public
   * @since v.1.0.0
   */
  const handleInputChange = (event) => {
    const { name, value } = event.target;
    setValues({ ...values, [name]: value });
    setErrors({ ...errors, [name]: event.target.validationMessage });
    setIsFormValid(event.target.closest('form').checkValidity());
    setIsInputChecked({ ...isInputChecked, [name]: true });
    setIsInputValid({ ...isInputValid, [name]: event.target.checkValidity() });
  };

  /**
   * @method
   * @name resetForm
   * @description –°–±—Ä–æ—Å —Ñ–æ—Ä–º—ã –ø–æ—Å–ª–µ –≤–≤–æ–¥–∞ –∑–Ω–∞—á–µ–Ω–∏–π<br> –ï—Å–ª–∏ –ø–æ—Å–ª–µ –≤–≤–æ–¥–∞ –∑–Ω–∞—á–µ–Ω–∏–π –≤ —Ñ–æ—Ä–º—É –Ω–æ–≤—ã–µ –∑–Ω–∞—á–µ–Ω–∏—è –Ω–µ –±—ã–ª–∏
   * –æ—Ç–ø—Ä–∞–≤–ª–µ–Ω—ã, —Ñ–æ—Ä–º–∞ –ø—Ä–∏–≤–æ–¥–∏—Ç—Å—è –≤ –∏–∑–Ω–∞—á–∞–ª—å–Ω–æ–µ –∞–∫—Ç—É–∞–ª—å–Ω–æ–µ —Å–æ—Å—Ç–æ—è–Ω–∏–µ —É—Å—Ç–∞–Ω–æ–≤–∫–æ–π —Å—Ç–µ–π—Ç–æ–≤ –≤ –Ω–µ–æ–±—Ö–æ–¥–∏–º—ã–µ —Å–æ—Å—Ç–æ—è–Ω–∏—è.
   *  –°–æ—Å—Ç–æ—è–Ω–∏—è —Å—Ç–µ–π—Ç–æ–≤ –ø–µ—Ä–µ–¥–∞—é—Ç—Å—è –∞—Ä–≥—É–º–µ–Ω—Ç–∞–º–∏ –∫–æ–ª–ª–±—ç–∫—É –º–µ—Ç–æ–¥–∞ resetForm.
   * @public
   * @since v.2.0.6
   */
  const resetForm = useCallback(
    (
      newValues = {},
      newErrors = {},
      newIsFormValid = false,
      newIsInputChecked = {},
      newIsInputValid = {},
    ) => {
      setValues(newValues);
      setErrors(newErrors);
      setIsFormValid(newIsFormValid);
      setIsInputChecked(newIsInputChecked);
      setIsInputValid(newIsInputValid);
      setFormError('');
    },
    [setValues, setErrors, setIsFormValid, setIsInputChecked, setIsInputValid],
  );

  return {
    values,
    errors,
    isFormValid,
    isInputChecked,
    isInputValid,
    handleInputChange,
    setValues,
    resetForm,
    formError,
    setFormError,
  };
}
