import { useEffect } from 'react';
import PopupWithForm from '../PopupWithForm/PopupWuthForm';
import { useFormWithValidation } from '../../hooks/useFormWithValidation';
import { forRegister as config } from '../../configs/configForComponents';
import PropTypes from 'prop-types';

/**
 * @module Register
 * @description –§—É–Ω–∫—Ü–∏–æ–Ω–∞–ª—å–Ω—ã–π –∫–æ–º–ø–æ–Ω–µ–Ω—Ç<br>
 * –ü–æ–ø–∞–ø, —Ñ–æ—Ä–º–∞ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏ –≤ –ø—Ä–∏–ª–æ–∂–µ–Ω–∏–µ.
 * @property {Boolean} isOpened - —Å—Ç–µ–π—Ç –æ—Ç–∫—Ä—ã—Ç–æ–≥–æ —Å–æ—Å—Ç–æ—è–Ω–∏—è –ø–æ–ø–∞–ø–∞
 * @property {Boolean} isRequestProcessing - —Å—Ç–µ–π—Ç —Å–æ—Å—Ç–æ—è–Ω–∏—è –∑–∞–ø—Ä–æ—Å–∞: true - –≤—ã–ø–æ–ª–Ω—è–µ—Ç—Å—è, false - –Ω–µ –≤—ã–ø–æ–ª–Ω—è–µ—Ç—Å—è
 * @property {Function} onClose - –∫–æ–ª–±—ç–∫, –∑–∞–∫—Ä—ã–≤–∞–µ—Ç –ø–æ–ø–∞–ø—ã –ø—Ä–∏ –∫–ª–∏–∫–µ –ø–æ –∫—Ä–µ—Å—Ç–∏–∫—É
 * @property {Function} onOverlayClick - –∫–æ–ª–±—ç–∫, –∑–∞–∫—Ä—ã–≤–∞–µ—Ç –ø–æ–ø–∞–ø—ã –ø—Ä–∏ –∫–ª–∏–∫–µ –ø–æ –æ–≤–µ—Ä–ª–µ—é
 * @property {Function} onRedirectLinkClick - –∫–æ–ª–±—ç–∫, –ø–µ—Ä–µ–≤–æ–¥–∏—Ç –Ω–∞ –¥—Ä—É–≥—É—é —Å—Ç—Ä–∞–Ω–∏—Ü—É
 * @property {Function} handleRegister - –∫–æ–ª–±—ç–∫, –æ—Ç–ø—Ä–∞–≤–ª—è–µ—Ç –∑–∞–ø—Ä–æ—Å –ø—Ä–∏ —Å–∞–±–º–∏—Ç–µ —Ñ–æ—Ä–º—ã
 * @returns {JSX} - JSX-—Ñ—Ä–∞–≥–º–µ–Ω—Ç —Ä–∞–∑–º–µ—Ç–∫–∏, —Ñ–æ—Ä–º–∞ –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏–∏ –≤ –ø—Ä–∏–ª–æ–∂–µ–Ω–∏–∏
 * @since v.1.0.0
 */
function Register({
  isOpened,
  isRequestProcessing,
  onClose,
  onOverlayClick,
  onRedirectLinkClickClick,
  handleRegister,
}) {
  const {
    FORM_TITLE,
    SUBMIT_BUTTON_TEXT,
    REDIRECT_TITLE_TEXT,
    REDIRECT_LINK_TEXT,
    EMAIL_LABEL,
    EMAIL_PLACEHOLDER,
    PASSWORD_LABEL,
    PASSWORD_PLACEHOLDER,
    NAME_LABEL,
    NAME_PLACEHOLDER,
    NAME_MIN_LENGTH,
    NAME_MAX_LENGTH,
  } = config;

  const {
    values,
    errors,
    isFormValid,
    handleInputChange,
    resetForm,
    formError,
    setFormError,
  } = useFormWithValidation();

  const { email, regPassword, name } = values;

  /**
   * @method handleSubmit
   * @description –û–±—Ä–∞–±–æ—Ç—á–∏–∫ —Å–∞–±–º–∏—Ç–∞ —Ñ–æ—Ä–º—ã —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏. –í–æ–∑–≤—Ä–∞—â–∞–µ—Ç —Å–æ–æ–±—â–µ–Ω–∏–µ –æ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏.
   * @param {Event} evt - —Å–æ–±—ã—Ç–∏–µ
   * @public
   * @returns {Object} —Å–æ–æ–±—â–µ–Ω–∏–µ –≤ —Ñ–æ—Ä–º–∞—Ç–µ {message: <—Å–æ–æ–±—â–µ–Ω–∏–µ>}
   * @since v.1.1.0
   */
  const handleSubmit = (evt) => {
    evt.preventDefault();
    const userRegistrationData = { email, password: regPassword, name };
    setFormError('');
    handleRegister(userRegistrationData, setFormError);
  };

  useEffect(() => {
    resetForm();
    //eslint-disable-next-line
  }, [isOpened]);

  return (
    <PopupWithForm
      formTitle={FORM_TITLE}
      submitButtonText={SUBMIT_BUTTON_TEXT}
      redirectTitleText={REDIRECT_TITLE_TEXT}
      redirectLinkText={REDIRECT_LINK_TEXT}
      isOpened={isOpened}
      onClose={onClose}
      onOverlayClick={onOverlayClick}
      onRedirectLinkClick={onRedirectLinkClickClick}
      isDisabled={!isFormValid}
      onSubmit={handleSubmit}
      formError={formError}
      isRequestProcessing={isRequestProcessing}
    >
      <>
        <ul className="form__inputs">
          <li className="form__field">
            <label className="form__input-label">{EMAIL_LABEL}</label>
            <input
              id="email"
              name="email"
              type="email"
              onChange={handleInputChange}
              value={email || ''}
              className={`form__input`}
              placeholder={EMAIL_PLACEHOLDER}
              disabled={isRequestProcessing}
              required
            />
            <span className="form__input-error" id="email-input-error">
              {errors.email || ''}
            </span>
          </li>
          <li className="form__field">
            <label className="form__input-label">{PASSWORD_LABEL}</label>
            <input
              id="regPassword"
              name="regPassword"
              type="password"
              onChange={handleInputChange}
              value={regPassword || ''}
              className="form__input"
              placeholder={PASSWORD_PLACEHOLDER}
              disabled={isRequestProcessing}
              required
            />
            <span className="form__input-error" id="regPassword-input-error">
              {errors.regPassword || ''}
            </span>
          </li>
          <li className="form__field">
            <label className="form__input-label">{NAME_LABEL}</label>
            <input
              id="name"
              name="name"
              type="text"
              onChange={handleInputChange}
              value={name || ''}
              className="form__input"
              placeholder={NAME_PLACEHOLDER}
              minLength={NAME_MIN_LENGTH}
              maxLength={NAME_MAX_LENGTH}
              disabled={isRequestProcessing}
              required
            />
            <span className="form__input-error" id="name-input-error">
              {errors.name || ''}
            </span>
          </li>
        </ul>
      </>
    </PopupWithForm>
  );
}

Register.propTypes = {
  isOpened: PropTypes.bool.isRequired,
  isRequestProcessing: PropTypes.bool.isRequired,
  onClose: PropTypes.func.isRequired,
  onOverlayClick: PropTypes.func.isRequired,
  onRedirectLinkClickClick: PropTypes.func.isRequired,
  handleRegister: PropTypes.func.isRequired,
};

export default Register;
