import { useEffect, useState } from 'react';
import NewsCardList from '../NewsCardList/NewsCardList';
import classNames from 'classnames';
import { forSearchResult as config } from '../../configs/configForComponents';
import PropTypes from 'prop-types';
import './SearchResult.css';

/**
 * @module SearchResult
 * @description –§—É–Ω–∫—Ü–∏–æ–Ω–∞–ª—å–Ω—ã–π –∫–æ–º–ø–æ–Ω–µ–Ω—Ç<br>
 * –ë–ª–æ–∫ –æ—Ç–æ–±—Ä–∞–∂–∞–µ—Ç —Å—Ç–∞—Ç—å–∏, –Ω–∞–π–¥–µ–Ω–Ω—ã–µ –≤ —Ä–µ–∑—É–ª—å—Ç–∞—Ç–µ –ø–æ–∏—Å–∫–∞ –ø–æ –∑–∞–ø—Ä–æ—Å—É –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è.<br>
 * @property {Array} searchResult - –º–∞—Å—Å–∏–≤ —Å –¥–∞–Ω–Ω—ã–º–∏ —Å—Ç–∞—Ç–µ–π, –Ω–∞–π–¥–µ–Ω—ã—Ö –≤ —Ä–µ–∑—É–ª—å—Ç–∞—Ç–µ –ø–æ–∏—Å–∫–∞ –ø–æ –∑–∞–ø—Ä–æ—Å—É –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è.
 * @property {Boolean} isLoggedIn - —Å—Ç–µ–π—Ç —Å–æ—Å—Ç—è–Ω–∏—è –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è: –∞–≤—Ç–æ—Ä–∏–∑–æ–≤–∞–Ω/–Ω–µ –∞–≤—Ç–æ—Ä–∏–∑–æ–≤–∞–Ω
 * @property {Function} openRegisterPopup - openRegisterPopup - –∫–æ–ª–±—ç–∫, –æ—Ç–∫—Ä—ã–≤–∞–µ—Ç –ø–æ–ø–∞–ø —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏
 * @property {Function} handleDeleteArticle - –∫–æ–ª–±—ç–∫, –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ —É–¥–∞–ª–µ–Ω–∏—è —Å—Ç–∞—Ç—å–∏
 * @property {Function} handleSaveArticle - –∫–æ–ª–±—ç–∫, –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ —Å–æ—Ö—Ä–∞–Ω–µ–Ω–∏—è —Å—Ç–∞—Ç—å–∏
 * @returns {JSX}
 * @since v.1.0.0
 */
function SearchResult({
  searchResult,
  isLoggedIn,
  openRegisterPopup,
  handleDeleteArticle,
  handleSaveArticle,
}) {
  const { TITLE, SHOW_MORE_BUTTON_TEXT } = config;

  const [cardsToRender, setCardsToRender] = useState([]);
  const [cardsToRenderQuantity, setCardsToRenderQuantity] = useState(3);
  const [isDisabled, setIsDisabled] = useState(false);

  const showMoreButtonClassName = classNames('search-result__button', {
    'search-result__button_disabled': isDisabled,
    'search-result__button_enabled': !isDisabled,
  });

  /**
   * @method handleClickShowMoreButton
   * @description –û–±—Ä–∞–±–æ—Ç—á–∏–∫ –∫–ª–∏–∫–∞ –ø–æ –∫–Ω–æ–ø–∫–µ "–ü–æ–∫–∞–∑–∞—Ç—å –µ—â—ë". –û—Ç–æ–±—Ä–∞–∂–∞–µ—Ç —Å–ª–µ–¥—É—é—â—É—é
   *  —á–∞—Å—Ç—å –∫–∞—Ä—Ç–æ—á–µ–∫ —Å—Ç–∞—Ç–µ–π, –Ω–æ –Ω–µ –±–æ–ª–µ–µ —Ç—Ä–µ—Ö –∑–∞ –æ–¥–∏–Ω —Ä–∞–∑.
   * @public
   * @since v.1.0.0
   */
  const handleClickShowMoreButton = () => {
    let cardsQuantity;
    if (searchResult.length - cardsToRenderQuantity > 3) {
      cardsQuantity = cardsToRenderQuantity + 3;
    } else {
      cardsQuantity = cardsToRenderQuantity + (searchResult.length - cardsToRenderQuantity);
      setIsDisabled(true);
    }
    setCardsToRenderQuantity(cardsQuantity);
  };

  useEffect(() => {
    setCardsToRender(searchResult.slice(0, cardsToRenderQuantity));
  }, [cardsToRenderQuantity, searchResult]);

  return (
    <section className="search-result">
      <h2 className="search-result__title">{TITLE}</h2>
      <NewsCardList
        cards={cardsToRender}
        isSavedNewsOpened={false}
        isLoggedIn={isLoggedIn}
        openRegisterPopup={openRegisterPopup}
        handleDeleteArticle={handleDeleteArticle}
        handleSaveArticle={handleSaveArticle}
      />
      <button
        onClick={handleClickShowMoreButton}
        disabled={isDisabled}
        type="button"
        className={showMoreButtonClassName}
      >
        {SHOW_MORE_BUTTON_TEXT}
      </button>
    </section>
  );
}

SearchResult.propTypes = {
  searchResult: PropTypes.arrayOf(
    PropTypes.shape({
      _id: PropTypes.oneOfType([PropTypes.string, PropTypes.number]).isRequired,
      source: PropTypes.string.isRequired,
      keyword: PropTypes.string.isRequired,
      title: PropTypes.string.isRequired,
      text: PropTypes.string.isRequired,
      date: PropTypes.string.isRequired,
      link: PropTypes.string.isRequired,
      image: PropTypes.string.isRequired,
    }),
  ).isRequired,
  isLoggedIn: PropTypes.bool.isRequired,
  openRegisterPopup: PropTypes.func.isRequired,
  handleDeleteArticle: PropTypes.func.isRequired,
  handleSaveArticle: PropTypes.func.isRequired,
};

export default SearchResult;
