import NewsCardList from '../NewsCardList/NewsCardList';
import SavedNewsHeader from '../SavedNewsHeader/SavedNewsHeader';
import SavedNewsInfo from '../SavedNewsInfo/SavedNewsInfo';
import PropTypes from 'prop-types';
import './SavedNews.css';

/**
 * @module SavedNews
 * @description –§—É–Ω–∫—Ü–∏–æ–Ω–∞–ª—å–Ω—ã–π –∫–æ–º–ø–æ–Ω–µ–Ω—Ç<br>
 * –û—Ç—Ä–∏—Å–æ–≤—ã–≤–∞–µ—Ç –∫–∞—Ä—Ç–æ—á–∫–∏ —Å–æ —Å—Ç–∞—Ç—å—è–º–∏, —Å–æ—Ö—Ä–∞–Ω–µ–Ω–Ω—ã–º–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–º –≤ —Å–≤–æ–µ–π –∫–æ–ª–ª–µ–∫—Ü–∏–∏.<br>
 * –û—Ç—Ä–∏—Å–æ–≤–∫–∞ –ø—Ä–æ–∏–∑–≤–æ–¥–∏—Ç—Å—è —á–∞—Å—Ç—è–º–∏ –ø–æ —Ç—Ä–∏ –∫–∞—Ä—Ç–æ—á–∫–∏.
 * @property {Boolean} isLoggedIn - —Å—Ç–µ–π—Ç —Å–æ—Å—Ç—è–Ω–∏—è –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è: –∞–≤—Ç–æ—Ä–∏–∑–æ–≤–∞–Ω/–Ω–µ –∞–≤—Ç–æ—Ä–∏–∑–æ–≤–∞–Ω
 * @property {Array} savedArticles - –º–∞—Å—Å–∏–≤ —Å –¥–∞–Ω–Ω—ã–º–∏ –æ —Å–æ—Ö—Ä–∞–Ω–µ–Ω–Ω—ã—Ö —Å—Ç–∞—Ç—å—è—Ö
 * @property {Function} handleDeleteArticle - –∫–æ–ª–±—ç–∫, –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ —É–¥–∞–ª–µ–Ω–∏—è —Å—Ç–∞—Ç—å–∏
 * @property {Object} props - –ø—Ä–æ–±—Ä–∞—Å—ã–≤–∞–µ–º—ã–µ –ø—Ä–æ–ø—Å—ã
 * @returns {JSX} - JSX-—Ñ—Ä–∞–≥–º–µ–Ω—Ç —Ä–∞–∑–º–µ—Ç–∫–∏, —Ñ–æ—Ä–º–∞ –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏–∏ –≤ –ø—Ä–∏–ª–æ–∂–µ–Ω–∏–∏
 * @since v.1.0.0
 */
function SavedNews({ isLoggedIn, savedArticles, handleDeleteArticle, ...props }) {
  return (
    <>
      <SavedNewsHeader isLoggedIn={isLoggedIn} {...props}>
        <SavedNewsInfo savedArticles={savedArticles} />
      </SavedNewsHeader>

      {savedArticles.length ? (
        <main className="saved-news">
          <NewsCardList
            cards={savedArticles}
            isSavedNewsOpened={true}
            isLoggedIn={isLoggedIn}
            handleDeleteArticle={handleDeleteArticle}
          />
        </main>
      ) : null}
    </>
  );
}

SavedNews.propTypes = {
  isLoggedIn: PropTypes.bool.isRequired,
  savedArticles: PropTypes.arrayOf(
    PropTypes.shape({
      _id: PropTypes.oneOfType([PropTypes.string, PropTypes.number]).isRequired,
      source: PropTypes.string.isRequired,
      keyword: PropTypes.string.isRequired,
      title: PropTypes.string.isRequired,
      text: PropTypes.string.isRequired,
      date: PropTypes.string.isRequired,
      link: PropTypes.string.isRequired,
      image: PropTypes.string.isRequired,
    }),
  ).isRequired,
  handleDeleteArticle: PropTypes.func.isRequired,
  props: PropTypes.objectOf(PropTypes.any),
};

export default SavedNews;
