import classNames from 'classnames';
import PropTypes from 'prop-types';
import './PopupWithForm.css';

/**
 * @module PopupWithForm
 * @description –§—É–Ω–∫—Ü–∏–æ–Ω–∞–ª—å–Ω—ã–π –∫–æ–º–ø–æ–Ω–µ–Ω—Ç<br>
 * –ü–æ–ø–∞–ø —Å —Ñ–æ—Ä–º–æ–π —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏ –∏–ª–∏ –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏–∏.<br>
 * @property {String} formTitle - –∑–∞–≥–æ–ª–æ–≤–æ–∫ —Ñ–æ—Ä–º—ã
 * @property {String} submitButtonText - —Ç–µ–∫—Å—Ç –Ω–∞ –∫–Ω–æ–ø–∫–µ —Å–∞–±–º–∏—Ç
 * @property {String} redirectTitleText - —Ç–µ–∫—Å—Ç –¥–æ —Å—Å—ã–ª–∫–∏ –ø–µ—Ä–µ–∞–¥—Ä–µ—Å–∞—Ü–∏–∏
 * @property {String} redirectLinkText - —Ç–µ–∫—Å—Ç —Å—Å—ã–ª–∫–∏ –ø–µ—Ä–µ–∞–¥—Ä–µ—Å–∞—Ü–∏–∏
 * @property {Boolean} isOpened - —Å—Ç–µ–π—Ç –æ—Ç–∫—Ä—ã—Ç–æ–≥–æ —Å–æ—Å—Ç–æ—è–Ω–∏—è –ø–æ–ø–∞–ø–∞
 * @property {JSX} children - JSX —Ñ—Ä–∞–≥–º–µ–Ω—Ç, –Ω–∞–ø—Ä–∏–º–µ—Ä –¥–æ–ø–æ–ª–Ω–∏—Ç–µ–ª—å–Ω—ã–π –∏–Ω–ø—É—Ç –¥–ª—è —Ñ–æ—Ä–º—ã —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏
 * @property {Function} onClose - –∫–æ–ª–±—ç–∫, –∑–∞–∫—Ä—ã–≤–∞–µ—Ç –ø–æ–ø–∞–ø –ø—Ä–∏ –∫–ª–∏–∫–µ –ø–æ –∫—Ä–µ—Å—Ç–∏–∫—É
 * @property {Function} onOverlayClick - –∫–æ–ª–±—ç–∫, –∑–∞–∫—Ä—ã–≤–∞–µ—Ç –ø–æ–ø–∞–ø –ø—Ä–∏ –∫–ª–∏–∫–µ –ø–æ –æ–≤–µ—Ä–ª–µ—é
 * @property {Function} onRedirectLinkClick - –∫–æ–ª–±—ç–∫, –≤—ã–∑—ã–≤–∞–µ—Ç—Å—è –ø—Ä–∏ –∫–ª–∏–∫–µ –ø–æ —Å—Å—ã–ª–∫–µ –ø–µ—Ä–µ–∞–¥—Ä–µ—Å–∞—Ü–∏–∏,
 * @property {Boolean} isDisabled - —Å—Ç–µ–π—Ç —Å–æ—Å—Ç–æ—è–Ω–∏—è –∫–Ω–æ–ø–∫–∏ —Å–∞–±–º–∏—Ç
 * @property {Function} onSubmit -  –∫–æ–ª–±—ç–∫, –≤—ã–∑—ã–≤–∞–µ—Ç—Å—è –ø—Ä–∏ —Å–∞–±–º–∏—Ç–µ —Ñ–æ—Ä–º—ã
 * @property {String} formError - —Ç–µ–∫—Å—Ç –æ—à–∏–±–∫–∏, –ø–æ–ª—É—á–µ–Ω–Ω–æ–π –æ—Ç —Å–µ—Ä–≤–µ—Ä–∞ –≤ —Ä–µ–∑—É–ª—å—Ç–∞—Ç–µ –æ–±—Ä–∞–±–æ—Ç–∫–∏ –∑–∞–ø—Ä–æ—Å–∞.
 * @returns {JSX}
 * @since v.1.0.0
 */
function PopupWithForm({
  formTitle,
  submitButtonText,
  redirectTitleText,
  redirectLinkText,
  isOpened,
  children,
  onClose,
  onOverlayClick,
  onRedirectLinkClick,
  isDisabled,
  onSubmit,
  formError,
  isRequestProcessing,
}) {
  const overlayClassName = classNames('popup', {
    'popup_opened page__overlay': isOpened,
  });

  const popupClassName = classNames('popup__container', {
    popup__container_mobile_opened: isOpened,
  });

  const submitButtonClassName = classNames('form__submit-button', {
    'form__submit-button_inactive': isDisabled || isRequestProcessing,
    'form__submit-button_active': !isDisabled && !isRequestProcessing,
  });

  return (
    <div onClick={onOverlayClick} className={overlayClassName}>
      <div className={popupClassName}>
        <button type="button" onClick={onClose} className="popup__close-button"></button>
        <form onSubmit={onSubmit} className="form popup__form">
          <h2 className="form__title">{formTitle}</h2>
          {children}
          <span className="form__submit-error-element">{formError || ''}</span>
          <button
            type="submit"
            className={submitButtonClassName}
            disabled={isDisabled || isRequestProcessing}
          >
            {submitButtonText}
          </button>
        </form>
        <p className="popup__redirect-title">
          {redirectTitleText}
          <span onClick={onRedirectLinkClick} className="popup__redirect-link">
            {redirectLinkText}
          </span>
        </p>
      </div>
    </div>
  );
}

PopupWithForm.propTypes = {
  formTitle: PropTypes.string.isRequired,
  submitButtonText: PropTypes.string.isRequired,
  redirectTitleText: PropTypes.string.isRequired,
  redirectLinkText: PropTypes.string.isRequired,
  isOpened: PropTypes.bool.isRequired,
  children: PropTypes.element,
  onClose: PropTypes.func.isRequired,
  onOverlayClick: PropTypes.func.isRequired,
  onRedirectLinkClick: PropTypes.func.isRequired,
  isDisabled: PropTypes.bool.isRequired,
  onSubmit: PropTypes.func.isRequired,
  formError: PropTypes.string,
  isRequestProcessing: PropTypes.bool.isRequired,
};

export default PopupWithForm;
