import NewsCard from '../NewsCard/NewsCard';
import classNames from 'classnames';
import PropTypes from 'prop-types';
import './NewsCardList.css';

/**
 * @module NewsCardList
 * @description –§—É–Ω–∫—Ü–∏–æ–Ω–∞–ª—å–Ω—ã–π –∫–æ–º–ø–æ–Ω–µ–Ω—Ç<br>
 * –°–ø–∏—Å–æ–∫ –∫–∞—Ä—Ç–æ—á–µ–∫ —Å—Ç–∞—Ç–µ–π.
 * @property {Array} cards - –º–∞—Å—Å–∏–≤ —Å –¥–∞–Ω–Ω—ã–º–∏ —Å—Ç–∞—Ç–µ–π
 * @property {Boolean} isSavedNewsOpened - —Å—Ç–µ–π—Ç —Å–æ—Å—Ç–æ—è–Ω–∏—è —Å—Ç—Ä–∞–Ω–∏—Ü—ã —Å —Å–æ—Ö—Ä–∞–Ω–µ–Ω–Ω—ã–º–∏ –Ω–æ–≤–æ—Å—Ç—è–º–∏
 * @property {Boolean} isLoggedIn -  —Å—Ç–µ–π—Ç —Å–æ—Å—Ç—è–Ω–∏—è –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è: –∞–≤—Ç–æ—Ä–∏–∑–æ–≤–∞–Ω/–Ω–µ –∞–≤—Ç–æ—Ä–∏–∑–æ–≤–∞–Ω
 * @property {Function} openRegisterPopup - –∫–æ–ª–±—ç–∫, –æ—Ç–∫—Ä—ã–≤–∞–µ—Ç –ø–æ–ø–∞–ø —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏
 * @property {Function} deleteArticle - –∫–æ–ª–±—ç–∫, —É–¥–∞–ª—è–µ—Ç —Å—Ç–∞—Ç—å—é
 * @property {Function} saveArticle - –∫–æ–ª–±—ç–∫, —Å–æ—Ö—Ä–∞–Ω—è–µ—Ç —Å—Ç–∞—Ç—å—é
 * @returns {JSX}
 * @since v.1.0.0
 */
function NewsCardList({
  cards,
  isSavedNewsOpened,
  isLoggedIn,
  openRegisterPopup,
  handleDeleteArticle,
  handleSaveArticle,
}) {
  const newsCardListClassName = classNames('news-card-list', {
    'search-result__cards': !isSavedNewsOpened,
    'saved-news__container': isSavedNewsOpened,
  });

  return (
    <ul className={newsCardListClassName}>
      {cards.map((card, index) => (
        <NewsCard
          key={index + 1}
          {...card}
          isSavedNewsOpened={isSavedNewsOpened}
          isLoggedIn={isLoggedIn}
          openRegisterPopup={openRegisterPopup}
          deleteArticle={handleDeleteArticle}
          saveArticle={handleSaveArticle}
        />
      ))}
    </ul>
  );
}

NewsCardList.propTypes = {
  cards: PropTypes.arrayOf(
    PropTypes.shape({
      _id: PropTypes.oneOfType([PropTypes.string, PropTypes.number]).isRequired,
      source: PropTypes.string.isRequired,
      keyword: PropTypes.string.isRequired,
      title: PropTypes.string.isRequired,
      text: PropTypes.string.isRequired,
      date: PropTypes.string.isRequired,
      link: PropTypes.string.isRequired,
      image: PropTypes.string.isRequired,
    }),
  ).isRequired,
  isSavedNewsOpened: PropTypes.bool.isRequired,
  isLoggedIn: PropTypes.bool.isRequired,
  openRegisterPopup: PropTypes.func,
  handleDeleteArticle: PropTypes.func.isRequired,
  handleSaveArticle: PropTypes.func,
};

export default NewsCardList;
