/**
 * @module App
 * @description –§—É–Ω–∫—Ü–∏–æ–Ω–∞–ª—å–Ω—ã–π –∫–æ–º–ø–æ–Ω–µ–Ω—Ç<br>
 * –ì–ª–∞–≤–Ω—ã–π –∫–æ–º–ø–æ–Ω–µ–Ω—Ç –ø—Ä–∏–ª–æ–∂–µ–Ω–∏—è<br>
 * –£–ø—Ä–∞–≤–ª—è–µ—Ç –≤—Å–µ–º–∏ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞–º–∏ –ø—Ä–∏–ª–æ–∂–µ–Ω–∏—è<br>
 * @returns {JSX}
 * @since v.1.0.0
 */
function App() {
  const [isLoginPopupOpened, setIsLoginPopupOpened] = useState(false);
  const [isRegisterPopupOpened, setIsRegisterPopupOpened] = useState(false);
  const [isRegSuccessTooltipOpened, setIsRegSuccessTooltipOpened] = useState(false);
  const [isMobileMenuOpened, setIsMobileMenuOpened] = useState(false);
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [isSearchDone, setIsSearchDone] = useState(false);
  const [isRequestProcessing, setIsRequestProcessing] = useState(false);
  const [isSearchInProgress, setIsSearchInProgress] = useState(false);
  const [isSearchFailed, setIsSearchFailed] = useState(false);
  const [currentUser, setCurrentUser] = useState({});
  const [savedNewsCards, setSavedNewsCards] = useState([]);
  const [foundNewsCards, setFoundNewsCards] = useState([]);

  const history = useHistory();

  /**
   * @method
   * @name closeAllPopups
   * @description –ü—É–±–ª–∏—á–Ω—ã–π –º–µ—Ç–æ–¥<br>
   * –°—Ç—Ä–µ–ª–æ—á–Ω–∞—è —Ñ—É–Ω–∫—Ü–∏—è, –∑–∞–∫—Ä—ã–≤–∞–µ—Ç –≤—Å–µ –ø–æ–ø–∞–ø—ã, —É–¥–∞–ª—è–µ—Ç —Å–ª—É—à–∞—Ç–µ–ª—å –Ω–∞–∂–∞—Ç–∏—è –∫–ª–∞–≤–∏—à–∏ Esc
   * @public
   * @since v.1.0.0
   */
  const closeAllPopups = () => {
    document.removeEventListener('keydown', handleEscClose);
    setIsLoginPopupOpened(false);
    setIsRegisterPopupOpened(false);
    setIsRegSuccessTooltipOpened(false);
    setIsMobileMenuOpened(false);
  };

  /**
   * @method handleEscClose
   * @description –û–±—Ä–∞–±–æ—Ç—á–∏–∫ –Ω–∞–∂–∞—Ç–∏—è –Ω–∞ –∫–ª–∞–≤–∏—à—É Escape<br>
   * –°—Ç—Ä–µ–ª–æ—á–Ω–∞—è —Ñ—É–Ω–∫—Ü–∏—è, –∑–∞–∫—Ä—ã–≤–∞–µ—Ç –ø–æ–ø–∞–ø –ø—Ä–∏ –Ω–∞–∂–∞—Ç–∏–∏ –∫–ª–∞–≤–∏—à–∏ Esc
   * @param {Event} evt - —Å–æ–±—ã—Ç–∏–µ
   * @public
   * @since v.1.0.0
   */
  const handleEscClose = (evt) => {
    if (evt.key === 'Escape') {
      closeAllPopups();
    }
  };

  /**
   * @method handleClickOnOverlay
   * @description –û–±—Ä–∞–±–æ—Ç—á–∏–∫ –∫–ª–∏–∫–∞ –ø–æ –æ–≤–µ—Ä–ª–µ—é<br>
   * –°—Ç—Ä–µ–ª–æ—á–Ω–∞—è —Ñ—É–Ω–∫—Ü–∏—è, –∑–∞–∫—Ä—ã–≤–∞–µ—Ç –ø–æ–ø–∞–ø –ø—Ä–∏ –∫–ª–∏–∫–µ –ø–æ –æ–≤–µ—Ä–ª–µ—é
   * @param {Event} evt - —Å–æ–±—ã—Ç–∏–µ
   * @public
   * @since v.1.0.0
   */
  const handleClickOnOverlay = (evt) => {
    /**
     * –ü—Ä–æ–≤–µ—Ä–∫–∞ –∏—Å—Ç–∏–Ω–Ω–æ—Å—Ç–∏ —É—Å–ª–æ–≤–∏—è - –∫–ª–∏–∫ –ø–æ –æ–≤–µ—Ä–ª–µ—é <br>
     * –ü—Ä–∏–º–µ—á–∞–Ω–∏–µ: —ç—Ç–æ—Ç –º–µ—Ç–æ–¥ –∏—Å–ø–æ–ª—å–∑—É–µ—Ç—Å—è –∫–∞–∫ –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ –≤ —Å–ª—É—à–∞—Ç–µ–ª–µ –∫–ª–∏–∫–∞ –Ω–∞ –æ–≤–µ—Ä–ª–µ–µ –ø–æ–ø–∞–ø–∞<br>
     * –ü–æ—ç—Ç–æ–º—É –≤ –¥–∞–Ω–Ω–æ–º —Å–ª—É—á–∞–µ —É—Å–ª–æ–≤–∏–µ –ø—Ä–æ–≤–µ—Ä—è–µ—Ç —Å–æ–≤–ø–∞–¥–µ–Ω–∏–µ –∫–ª–∏–∫–∞ –∏–º–µ–Ω–Ω–æ –Ω–∞ –æ–≤–µ—Ä–ª–µ–µ –ø–æ–ø–∞–ø–∞
     * @ignore
     */
    if (evt.target === evt.currentTarget) {
      closeAllPopups();
    }
  };

  /**
   * @method  handleClickLogIn
   * @description –û–±—Ä–∞–±–æ—Ç—á–∏–∫ –∫–ª–∏–∫–∞ –ø–æ –∫–Ω–æ–ø–∫–µ "–í–æ–π—Ç–∏"<br>
   * –°—Ç—Ä–µ–ª–æ—á–Ω–∞—è —Ñ—É–Ω–∫—Ü–∏—è, –æ—Ç–∫—Ä—ã–≤–∞–µ—Ç –ø–æ–ø–∞–ø –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏–∏ –ø—Ä–∏ –∫–ª–∏–∫–µ –ø–æ –∫–Ω–æ–ø–∫–µ "–ê–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏—è" –∏–ª–∏ —Å—Å—ã–ª–∫–µ "–í–æ–π—Ç–∏"
   * @public
   * @since v.1.0.0
   */
  const handleClickLogIn = () => {
    closeAllPopups();
    setIsLoginPopupOpened(true);
    document.addEventListener('keydown', handleEscClose);
  };

  /**
   * @method  handleClickLogOut
   * @description –û–±—Ä–∞–±–æ—Ç—á–∏–∫ –∫–ª–∏–∫–∞ –ø–æ –∫–Ω–æ–ø–∫–µ "–í—ã–π—Ç–∏"<br>
   * –°—Ç—Ä–µ–ª–æ—á–Ω–∞—è —Ñ—É–Ω–∫—Ü–∏—è, –≤—ã—Ö–æ–¥ –∏–∑ –∞–∫–∫–∞—É–Ω—Ç–∞ –ø—Ä–∏ –∫–ª–∏–∫–µ –ø–æ –∫–Ω–æ–ø–∫–µ —Å –∏–∫–æ–Ω–∫–æ–π "–í—ã–π—Ç–∏"
   * @public
   * @since v.1.0.0
   */
  const handleClickLogOut = () => {
    setIsLoggedIn(false);
    removeTokenFromStorage();
    removeUserDataFromStorage();
    removeSavedNewsFromStorage();
    removeFoundNewsFromStorage();
    setCurrentUser({});
    setFoundNewsCards([]);
    setIsSearchDone(false);
    history.push(to.MAIN);
  };

  /**
   * @method  handleClickRegister
   * @description –û–±—Ä–∞–±–æ—Ç—á–∏–∫ –∫–ª–∏–∫–∞ –ø–æ –∫–Ω–æ–ø–∫–µ "–ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è"<br>
   * –°—Ç—Ä–µ–ª–æ—á–Ω–∞—è —Ñ—É–Ω–∫—Ü–∏—è, –æ—Ç–∫—Ä—ã–≤–∞–µ—Ç –ø–æ–ø–∞–ø —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏ –ø—Ä–∏ –∫–ª–∏–∫–µ –ø–æ —Å—Å—ã–ª–∫–µ "–ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è"
   * @public
   * @since v.1.0.0
   */
  const handleClickRegister = () => {
    closeAllPopups();
    setIsRegisterPopupOpened(true);
    document.addEventListener('keydown', handleEscClose);
  };

  /**
   * @method  handleRegister
   * @description –û–±—Ä–∞–±–æ—Ç—á–∏–∫ –∑–∞–ø—Ä–æ—Å–∞ –Ω–∞ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—é –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è<br>
   * –°—Ç—Ä–µ–ª–æ—á–Ω–∞—è —Ñ—É–Ω–∫—Ü–∏—è, –æ—Ç–ø—Ä–∞–≤–ª—è–µ—Ç –∑–∞–ø—Ä–æ—Å –Ω–∞ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—é –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è, –ø–µ—Ä–µ–¥–∞–µ—Ç –≤–≤–µ–¥–µ–Ω–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ
   * @param {Object} userData - –æ–±—ä–µ–∫—Ç —Å —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–æ–Ω–Ω—ã–º–∏ –¥–∞–Ω–Ω—ã–º–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
   * @param {String} userData.email - –µ–º—ç–π–ª
   * @param {String} userData.password - –ø–∞—Ä–æ–ª—å
   * @param {String} userData.name - –∏–º—è
   * @param {Function} showError - –∫–æ–ª–±—ç–∫, –æ—Ç–æ–±—Ä–∞–∂–∞–µ—Ç –æ—à–∏–±–∫—É —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏, –ø–æ–ª—É—á–µ–Ω–Ω—É—é –æ—Ç —Å–µ—Ä–≤–µ—Ä–∞
   * @public
   * @since v.1.0.0
   */
  const handleRegister = ({ email, password, name }, showError) => {
    setIsRequestProcessing(true);
    register(email, password, name)
      .then((serverError) => {
        if (!serverError) {
          closeAllPopups();
          setIsRegSuccessTooltipOpened(true);
          document.addEventListener('keydown', handleEscClose);
        } else {
          if (serverError.statusCode) {
            serverError.message = INVALID_REGISTRATION_DATA_MESSAGE;
          }
          showError(serverError.message);
        }
      })
      .catch((err) => console.log({ err }))
      .finally(() => {
        setIsRequestProcessing(false);
      });
  };

  /**
   * @method  handleLogin
   * @description –û–±—Ä–∞–±–æ—Ç—á–∏–∫ –∑–∞–ø—Ä–æ—Å–∞ –Ω–∞ –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏—é –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è<br>
   * –°—Ç—Ä–µ–ª–æ—á–Ω–∞—è —Ñ—É–Ω–∫—Ü–∏—è, –æ—Ç–ø—Ä–∞–≤–ª—è–µ—Ç –∑–∞–ø—Ä–æ—Å –Ω–∞ –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏—é –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è, –ø–µ—Ä–µ–¥–∞–µ—Ç –≤–≤–µ–¥–µ–Ω–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ
   * @param {Object} userData - –æ–±—ä–µ–∫—Ç —Å —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–æ–Ω–Ω—ã–º–∏ –¥–∞–Ω–Ω—ã–º–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
   * @param {String} userData.email - –µ–º—ç–π–ª
   * @param {String} userData.password - –ø–∞—Ä–æ–ª—å
   * @param {Function} showError - –∫–æ–ª–±—ç–∫, –æ—Ç–æ–±—Ä–∞–∂–∞–µ—Ç –æ—à–∏–±–∫—É —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏, –ø–æ–ª—É—á–µ–Ω–Ω—É—é –æ—Ç —Å–µ—Ä–≤–µ—Ä–∞
   * @public
   * @since v.1.0.0
   */
  const handleLogin = ({ email, password }, showError) => {
    setIsRequestProcessing(true);
    login(email, password)
      .then((res) => {
        if (res.token) {
          setTokenToStorage(res.token);
          Promise.all([getUserDataFromDataBase(), getSavedNewsFromDataBase()])
            .then(([userData, savedNews]) => {
              if (userData.message) {
                showError(userData.message);
              } else if (savedNews.message) {
                showError(savedNews.message);
              } else {
                setCurrentUser(userData);
                setUserDataToStorage(userData);
                setSavedNewsToStorage(savedNews);
                setSavedNewsCards(savedNews);
                setIsLoggedIn(true);
                closeAllPopups();
              }
            })
            .catch((err) => {
              console.log(err);
            });
        } else {
          if (res.statusCode) {
            res.message = INVALID_AUTHORIZATION_DATA_MESSAGE;
          }
          showError(res.message);
        }
      })
      .catch((err) => {
        console.log({ err });
      })
      .finally(() => {
        setIsRequestProcessing(false);
      });
  };

  /**
   * @method  handleClickMenuButton
   * @description –û–±—Ä–∞–±–æ—Ç—á–∏–∫ –∫–ª–∏–∫–∞ –ø–æ –∫–Ω–æ–ø–∫–µ "–ú–æ–±–∏–ª—å–Ω–æ–µ –º–µ–Ω—é"<br>
   * –°—Ç—Ä–µ–ª–æ—á–Ω–∞—è —Ñ—É–Ω–∫—Ü–∏—è, –æ—Ç–∫—Ä—ã–≤–∞–µ—Ç –º–æ–±–∏–ª—å–Ω–æ–µ –º–µ–Ω—é, –∑–∞–∫—Ä—ã–≤–∞–µ—Ç –º–æ–±–∏–ª—å–Ω–æ–µ –º–µ–Ω—é –∏–ª–∏ –æ—Ç–∫—Ä—ã—Ç—ã–π –ø–æ–ø–∞–ø
   *  –ø—Ä–∏ –∫–ª–∏–∫–µ –ø–æ –∫–Ω–æ–ø–∫–µ –º–µ–Ω—é –Ω–∞ –º–æ–±–∏–ª—å–Ω–æ–º —Ä–∞–∑—Ä–µ—à–µ–Ω–∏–∏
   * @public
   * @since v.1.0.0
   */
  const handleClickMenuButton = () => {
    if (isMobileMenuOpened || isLoginPopupOpened || isRegisterPopupOpened) {
      closeAllPopups();
    } else {
      setIsMobileMenuOpened(true);
    }
  };

  /**
   * @method findAndUpdateFoundNewsCard
   * @description –ü—É–±–ª–∏—á–Ω—ã–π –º–µ—Ç–æ–¥<br>
   * –ù–∞—Ö–æ–¥–∏—Ç —Å—Ç–∞—Ç—å—é –≤ —Ä–µ–∑—É–ª—å—Ç–∞—Ç–∞—Ö –ø–æ–∏—Å–∫–∞ –∏ –æ–±–Ω–æ–≤–ª—è–µ—Ç –µ–µ id.
   * –ï—Å–ª–∏ —Å—Ç–∞—Ç—å—è —Å–æ—Ö—Ä–∞–Ω—è–µ—Ç—Å—è - id, –ø—Ä–∏—Å–≤–æ–µ–Ω–Ω—ã–π –ø—Ä–∏ –æ—Ç—Ä–∏—Å–æ–≤–∫–µ, –∑–∞–º–µ–Ω—è–µ—Ç—Å—è –Ω–∞ id, –ø—Ä–∏—Å–≤–æ–µ–Ω–Ω—ã–π –ø—Ä–∏ —Å–æ—Ö—Ä–∞–Ω–µ–Ω–∏–∏ –≤ –ë–î.<br>
   * –ï—Å–ª–∏ —Å—Ç–∞—Ç—å—è —É–¥–∞–ª—è–µ—Ç—Å—è - id –∑–∞–º–µ–Ω—è–µ—Ç—Å—è –Ω–∞ –µ—ë –∏–Ω–¥–µ–∫—Å –≤ –º–∞—Å—Å–∏–≤–µ.
   * @param {Number| String} id - —Å—É—â–µ—Å—Ç–≤—É—â–∏–π id  —Å—Ç–∞—Ç—å–∏
   * @param {Array} cardsArray - –º–∞—Å—Å–∏–≤ —Å—Ç–∞—Ç–µ–π (—Ä–µ–∑—É–ª—å—Ç–∞—Ç—ã –ø–æ–∏—Å–∫–∞)
   * @param {Object} newCard - –Ω–æ–≤—ã–π id (—Å—Ç–∞—Ç—å—è —Å–æ—Ö—Ä–∞–Ω—è–µ—Ç—Å—è), undefined –µ—Å–ª–∏ —Å—Ç–∞—Ç—å—è —É–¥–∞–ª—è–µ—Ç—Å—è
   * @public
   * @since v.1.1.0
   */
  const findAndUpdateFoundNewsCard = (id, cardsArray, newCard) => {
    const arrayWithUpdatedCard = cardsArray.map((card, index) =>
      card._id === id
        ? (() => {
            card._id = newCard._id ? newCard._id : index + 1;
            return card;
          })()
        : card,
    );
    setFoundNewsCards(arrayWithUpdatedCard);
    setFoundNewsToStorage(arrayWithUpdatedCard);
  };

  /**
   * @method handleDeleteArticle
   * @description –û–±—Ä–∞–±–æ—Ç—á–∏–∫ –∑–∞–ø—Ä–æ—Å–∞ –Ω–∞ —É–¥–∞–ª–µ–Ω–∏–µ —Å—Ç–∞—Ç—å–∏<br>
   * @param {Object} article - –æ–±—ä–µ–∫—Ç —Å –¥–∞–Ω–Ω—ã–º–∏ –∫–∞—Ä—Ç–æ—á–∫–∏ —Å—Ç–∞—Ç—å–∏
   * @param {String} article._id - id —Å—Ç–∞—Ç—å–∏, –∫–æ—Ç–æ—Ä—É—é –Ω–∞–¥–æ —É–¥–∞–ª–∏—Ç—å (—ç—Ç–æ id, –ø—Ä–∏—Å–≤–æ–µ–Ω–Ω—ã–π –ë–î
   *  –ø—Ä–∏ —Å–æ—Ö—Ä–∞–Ω–µ–Ω–∏–∏ —Å—Ç–∞—Ç—å–∏)
   * @public
   * @since v.1.1.0
   */
  const handleDeleteArticle = (article) => {
    deleteArticle(article._id)
      .then((res) => {
        const resultCardsArray = savedNewsCards.filter((—Åard) => {
          return —Åard._id !== article._id;
        });
        setSavedNewsCards(resultCardsArray);
        setSavedNewsToStorage(resultCardsArray);
        findAndUpdateFoundNewsCard(article._id, foundNewsCards, { _id: undefined });
      })
      .catch((err) => {
        console.log(err);
      });
  };

  /**
   * @method handleSaveArticle
   * @description –û–±—Ä–∞–±–æ—Ç—á–∏–∫ –∑–∞–ø—Ä–æ—Å–∞ –Ω–∞ —Å–æ—Ö—Ä–∞–Ω–µ–Ω–∏–µ —Å—Ç–∞—Ç—å–∏<br>
   * @param {Object} article - –æ–±—ä–µ–∫—Ç —Å –¥–∞–Ω–Ω—ã–º–∏ —Å—Ç–∞—Ç—å–∏, –∫–æ—Ç–æ—Ä—É—é –Ω–∞–¥–æ —Å–æ—Ö—Ä–∞–Ω–∏—Ç—å
   * @param {Number} id - id —Å—Ç–∞—Ç—å–∏, –∫–æ—Ç–æ—Ä—É—é –Ω—É–∂–Ω–æ —Å–æ—Ö—Ä–∞–Ω–∏—Ç—å. (—ç—Ç–æ—Ç id —Ä–∞–≤–µ–Ω –∏–Ω–¥–µ–∫—Å—É —Å—Ç–∞—Ç—å–∏
   *  –≤ –º–∞—Å—Å–∏–≤–µ –Ω–∞–π–¥–µ–Ω–Ω—ã—Ö –Ω–æ–≤–æ—Å—Ç–µ–π)
   * @public
   * @since v.1.1.0
   */
  const handleSaveArticle = (article, id) => {
    addArticleToSavedNews(article)
      .then((newCard) => {
        const resultCardsArray = savedNewsCards.concat(newCard);
        setSavedNewsCards(resultCardsArray);
        setSavedNewsToStorage(resultCardsArray);
        findAndUpdateFoundNewsCard(id, foundNewsCards, newCard);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  /**
   * @method handleSearchFormSubmit
   * @description –û–±—Ä–∞–±–æ—Ç—á–∏–∫ —Å–∞–±–º–∏—Ç–∞ —Ñ–æ—Ä–º—ã –ø–æ–∏—Å–∫–∞<br>
   * –û–±—Ä–∞–±–∞—Ç—ã–≤–∞–µ—Ç –ø–æ–∏—Å–∫–æ–≤—ã–π –∑–∞–ø—Ä–æ—Å –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è. –ü—Ä–∏–Ω–∏–º–∞–µ—Ç –∞—Ä–≥—É–º–µ–Ω—Ç–æ–º –∑–∞–ø—Ä–æ—Å, –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç –º–∞—Å—Å–∏–≤
   *  –Ω–∞–π–¥–µ–Ω–Ω—ã—Ö —Å—Ç–∞—Ç–µ–π –∏–ª–∏ –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—é –æ —Ä–µ–∑—É–ª—å—Ç–∞—Ç–∞—Ö –æ–±—Ä–∞–±–æ—Ç–∫–∏ –∑–∞–ø—Ä–æ—Å–∞.
   * @param {String} userQuery - –∑–∞–ø—Ä–æ—Å
   * @public
   * @since v.1.1.0
   */
  const handleSearchFormSubmit = (userQuery) => {
    setIsSearchFailed(false);
    setIsSearchDone(false);
    setIsSearchInProgress(true);
    setFoundNewsCards([]);
    getArticlesFromNewsApi(userQuery)
      .then((res) => {
        const formattedNewsCards = res.articles.map((article, index) => ({
          _id: index + 1,
          source: article.source.name,
          keyword: userQuery[0].toUpperCase().concat(userQuery.slice(1).toLowerCase()),
          title: article.title,
          text: article.description,
          date: getFormattedDate(article.publishedAt),
          link: article.url,
          image: article.urlToImage,
        }));
        setFoundNewsCards(formattedNewsCards);
        if (isLoggedIn) {
          setFoundNewsToStorage(formattedNewsCards);
        }
      })
      .catch((err) => {
        console.log(err);
        setIsSearchFailed(true);
      })
      .finally(() => {
        setIsSearchInProgress(false);
        setIsSearchDone(true);
      });
  };

  useEffect(() => {
    const userDataFromStorage = getUserDataFromStorage();
    const tokenFromStorage = getTokenFromStorage();
    const savedNewsFromStorage = getSavedNewsFromStorage();
    const foundNewsFromStorage = getFoundNewsFromStorage();
    if (userDataFromStorage && tokenFromStorage) {
      setCurrentUser(userDataFromStorage);
      setSavedNewsCards(savedNewsFromStorage);
      if (foundNewsFromStorage) {
        setFoundNewsCards(foundNewsFromStorage);
        setIsSearchDone(true);
      }
      setIsLoggedIn(true);
    } else if (history.location.state) {
      setIsLoginPopupOpened(true);
    }
  }, [history]);

  return (
    <>
      <CurrentUserContext.Provider value={currentUser}>
        <Switch>
          <Route path={to.MAIN} exact>
            <Header
              isLoggedIn={isLoggedIn}
              onLogInClick={handleClickLogIn}
              onLogOutClick={handleClickLogOut}
              onMenuButtonClick={handleClickMenuButton}
              isMobileMenuOpened={isMobileMenuOpened}
              isPopupOpened={isLoginPopupOpened || isRegisterPopupOpened}
              onOverlayClick={handleClickOnOverlay}
            >
              <SearchForm
                handleSearchFormSubmit={handleSearchFormSubmit}
                isSearchInProgress={isSearchInProgress}
              />
            </Header>
            <Main
              isSearchInProgress={isSearchInProgress}
              isSearchDone={isSearchDone}
              isSearchFailed={isSearchFailed}
              searchResult={foundNewsCards}
              isLoggedIn={isLoggedIn}
              openRegisterPopup={handleClickRegister}
              handleDeleteArticle={handleDeleteArticle}
              handleSaveArticle={handleSaveArticle}
            />
          </Route>

          <ProtectedRoute
            path={to.SAVED_NEWS}
            isLoggedIn={isLoggedIn}
            savedArticles={savedNewsCards}
            component={SavedNews}
            onLogInClick={handleClickLogIn}
            onLogOutClick={handleClickLogOut}
            onMenuButtonClick={handleClickMenuButton}
            isMobileMenuOpened={isMobileMenuOpened}
            isPopupOpened={isLoginPopupOpened || isRegisterPopupOpened}
            onOverlayClick={handleClickOnOverlay}
            handleDeleteArticle={handleDeleteArticle}
          />

          <Route path={to.ANY_ROUTE}>
            <PageNotFound />
          </Route>
        </Switch>

        <Footer />
        <Login
          isOpened={isLoginPopupOpened}
          onClose={closeAllPopups}
          onOverlayClick={handleClickOnOverlay}
          onRedirectLinkClick={handleClickRegister}
          handleLogin={handleLogin}
          isRequestProcessing={isRequestProcessing}
        />
        <Register
          isOpened={isRegisterPopupOpened}
          onClose={closeAllPopups}
          onOverlayClick={handleClickOnOverlay}
          onRedirectLinkClickClick={handleClickLogIn}
          handleRegister={handleRegister}
          isRequestProcessing={isRequestProcessing}
        />
        <InfoTooltip
          isOpened={isRegSuccessTooltipOpened}
          onClose={closeAllPopups}
          onOverlayClick={handleClickOnOverlay}
          onRedirectLinkClick={handleClickLogIn}
        />
      </CurrentUserContext.Provider>
    </>
  );
}

export default App;
