import About from '../About/About';
import NoResult from '../NoResult/NoResult';
import Preloader from '../Preloader/Preloader';
import SearchResult from '../SearchResult/SearchResult';
import PropTypes from 'prop-types';
import './Main.css';

/**
 * @module Main
 * @description –§—É–Ω–∫—Ü–∏–æ–Ω–∞–ª—å–Ω—ã–π –∫–æ–º–ø–æ–Ω–µ–Ω—Ç<br>
 * –ë–ª–æ–∫ —Å –æ—Å–Ω–æ–≤–Ω—ã–º –∫–æ–Ω—Ç–µ–Ω—Ç–æ–º —Å—Ç—Ä–∞–Ω–∏—Ü—ã.<br>
 * @property {Boolean} isLoggedIn - —Å—Ç–µ–π—Ç —Å–æ—Å—Ç—è–Ω–∏—è –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è: –∞–≤—Ç–æ—Ä–∏–∑–æ–≤–∞–Ω/–Ω–µ –∞–≤—Ç–æ—Ä–∏–∑–æ–≤–∞–Ω
 * @property {Boolean} isSearchDone - —Å—Ç–µ–π—Ç, –ø–æ–∫–∞–∑—ã–≤–∞—é—â–∏–π –æ—Ç–ø—Ä–∞–≤–ª—è–ª –ª–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –∑–∞–ø—Ä–æ—Å—ã.
 * @property {Boolean} isSearchInProgress - —Å—Ç–µ–π—Ç —Å–æ—Å—Ç–æ—è–Ω–∏—è –∑–∞–ø—Ä–æ—Å–∞: true - –≤—ã–ø–æ–ª–Ω—è–µ—Ç—Å—è, false - –Ω–µ –≤—ã–ø–æ–ª–Ω—è–µ—Ç—Å—è
 * @property {Boolean} isSearchFailed - —Å—Ç–µ–π—Ç —Å–æ—Å—Ç–æ—è–Ω–∏—è —Ä–µ–∑—É–ª—å—Ç–∞—Ç–∞ –∑–∞–ø—Ä–æ—Å–∞: true - –Ω–µ —É—Å–ø–µ—à–Ω—ã–π, false -  —É—Å–ø–µ—à–Ω—ã–π
 * @property {Array} searchResult - –º–∞—Å—Å–∏–≤ —Å –¥–∞–Ω–Ω—ã–º–∏ —Ä–µ–∑—É–ª—å—Ç–∞—Ç–∞ –ø–æ–∏—Å–∫–∞
 * @property {Function} openRegisterPopup - –∫–æ–ª–±—ç–∫, –æ—Ç–∫—Ä—ã–≤–∞–µ—Ç –ø–æ–ø–∞–ø —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏
 * @property {Function} handleDeleteArticle - –∫–æ–ª–±—ç–∫, –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ —É–¥–∞–ª–µ–Ω–∏—è —Å—Ç–∞—Ç—å–∏
 * @property {Function} handleSaveArticle - –∫–æ–ª–±—ç–∫, –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ —Å–æ—Ö—Ä–∞–Ω–µ–Ω–∏—è —Å—Ç–∞—Ç—å–∏
 * @returns {JSX}
 * @since v.1.0.0
 */
function Main({
  isLoggedIn,
  isSearchDone,
  isSearchInProgress,
  isSearchFailed,
  searchResult,
  openRegisterPopup,
  handleDeleteArticle,
  handleSaveArticle,
}) {
  return (
    <>
      <main className="content">
        {isSearchInProgress && <Preloader />}
        {isSearchDone ? (
          !isSearchFailed && searchResult.length > 0 ? (
            <SearchResult
              searchResult={searchResult}
              isLoggedIn={isLoggedIn}
              openRegisterPopup={openRegisterPopup}
              handleDeleteArticle={handleDeleteArticle}
              handleSaveArticle={handleSaveArticle}
            />
          ) : (
            <NoResult isSearchFailed={isSearchFailed} />
          )
        ) : null}

        <About />
      </main>
    </>
  );
}

Main.propTypes = {
  isLoggedIn: PropTypes.bool.isRequired,
  isSearchDone: PropTypes.bool.isRequired,
  isSearchInProgress: PropTypes.bool.isRequired,
  isSearchFailed: PropTypes.bool.isRequired,
  searchResult: PropTypes.arrayOf(
    PropTypes.shape({
      _id: PropTypes.oneOfType([PropTypes.string, PropTypes.number]).isRequired,
      source: PropTypes.string.isRequired,
      keyword: PropTypes.string.isRequired,
      title: PropTypes.string.isRequired,
      text: PropTypes.string.isRequired,
      date: PropTypes.string.isRequired,
      link: PropTypes.string.isRequired,
      image: PropTypes.string.isRequired,
    }),
  ).isRequired,
  openRegisterPopup: PropTypes.func.isRequired,
  handleDeleteArticle: PropTypes.func.isRequired,
  handleSaveArticle: PropTypes.func.isRequired,
};

export default Main;
