import Menu from '../Menu/Menu';
import classNames from 'classnames';
import { forHeader as config } from '../../configs/configForComponents';
import PropTypes from 'prop-types';
import './Header.css';

/**
 * @module Header
 * @description –§—É–Ω–∫—Ü–∏–æ–Ω–∞–ª—å–Ω—ã–π –∫–æ–º–ø–æ–Ω–µ–Ω—Ç<br>
 * –•—ç–¥–µ—Ä, –±–ª–æ–∫ —Å–æ–¥–µ—Ä–∂–∏—Ç –ª–æ–≥–æ—Ç–∏–ø –∏ –º–µ–Ω—é –Ω–∞–≤–∏–≥–∞—Ü–∏–∏ –ø–æ —Å–∞–π—Ç—É.<br>
 * –ü—Ä–∏–Ω–∏–º–∞–µ—Ç –≤ –ø—Ä–æ–ø—Å–∞—Ö –æ–±—ä–µ–∫—Ç —Å –Ω–∞—Å—Ç—Ä–æ–π–∫–∞–º–∏ –±–ª–æ–∫–∞ - config –∏ –ø–∞—Ä–∞–º–µ—Ç—Ä—ã.
 * @property {Boolean} isLoggedIn - —Å—Ç–µ–π—Ç —Å–æ—Å—Ç—è–Ω–∏—è –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è: –∞–≤—Ç–æ—Ä–∏–∑–æ–≤–∞–Ω/–Ω–µ –∞–≤—Ç–æ—Ä–∏–∑–æ–≤–∞–Ω
 * @property {Function} onLogInClick - –∫–æ–ª–±—ç–∫, –≤—ã–∑—ã–≤–∞–µ—Ç—Å—è –ø—Ä–∏ –∫–ª–∏–∫–µ –ø–æ –∫–Ω–æ–ø–∫–µ "–ê–≤—Ç–æ—Ä–∏–∑–æ–≤–∞—Ç—å—Å—è"
 *  –∏–ª–∏ —Å—Å—ã–ª–∫–µ "–í–æ–π—Ç–∏", –æ—Ç–∫—Ä—ã–≤–∞–µ—Ç –ø–æ–ø–∞–ø –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏–∏
 * @property {Function} onLogOutClick - –∫–æ–ª–±—ç–∫, –ø–µ—Ä–µ–≤–æ–¥–∏—Ç —Å–æ—Å—Ç–æ—è–Ω–∏–µ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è –≤ "–Ω–µ –∞–≤—Ç–æ—Ä–∏–∑–æ–≤–∞–Ω"
 * @property {JSX} children - JSX-—Ñ—Ä–∞–≥–º–µ–Ω—Ç, –Ω–∞ –≥–ª–∞–≤–Ω–æ–π —Å—Ç—Ä–∞–Ω–∏—Ü–µ —á–µ—Ä–µ–∑ –Ω–µ–≥–æ –ø–µ—Ä–µ–¥–∞–µ—Ç—Å—è —Ñ–æ—Ä–º–∞ –ø–æ–∏—Å–∫–∞ –Ω–æ–≤–æ—Å—Ç–µ–π
 * @property {Function} onMenuButtonClick - –∫–æ–ª–±—ç–∫, –æ—Ç–∫—Ä—ã–≤–∞–µ—Ç —Å–∫—Ä—ã—Ç–æ–µ –º–µ–Ω—é –Ω–∞ –º–æ–±–∏–ª—å–Ω–æ–º —Ä–∞–∑—Ä–µ—à–µ–Ω–∏–∏
 * @property {Boolean} isMobileMenuOpened - —Å—Ç–µ–π—Ç –æ—Ç–∫—Ä—ã—Ç–æ–≥–æ —Å–æ—Å—Ç–æ—è–Ω–∏—è –º–æ–±–∏–ª—å–Ω–æ–≥–æ –º–µ–Ω—é
 * @property {Boolean} isPopupOpened - —Å—Ç–µ–π—Ç –æ—Ç–∫—Ä—ã—Ç–æ–≥–æ —Å–æ—Å—Ç–æ—è–Ω–∏—è –æ–¥–Ω–æ–≥–æ –∏–∑ –ø–æ–ø–∞–ø–æ–≤
 * @property {Function} onOverlayClick - –∫–æ–ª–±—ç–∫, –∑–∞–∫—Ä—ã–≤–∞–µ—Ç –ø–æ–ø–∞–ø—ã –ø—Ä–∏ –∫–ª–∏–∫–µ –ø–æ –æ–≤–µ—Ä–ª–µ—é
 * @returns {JSX}
 * @since v.1.0.0
 */
function Header({
  isLoggedIn,
  onLogInClick,
  onLogOutClick,
  children,
  onMenuButtonClick,
  isMobileMenuOpened,
  isPopupOpened,
  onOverlayClick,
}) {
  const { HEADER_LOGO_TEXT } = config;

  const headerContainerClassName = classNames('header__container', {
    header__container_mobile: isMobileMenuOpened,
  });

  const headerMobileMenuButtonClassName = classNames('header__menu-button', {
    'header__menu-button_not-pressed': !(isMobileMenuOpened && isPopupOpened),
    'header__menu-button_pressed': isMobileMenuOpened || isPopupOpened,
  });

  const headerMobileMenuOverlayClassName = classNames('overlay', {
    'header__menu-mobile-overlay': isMobileMenuOpened,
  });

  return (
    <header className="header">
      <div className={headerContainerClassName}>
        <p className="header__logo">{HEADER_LOGO_TEXT}</p>
        <Menu
          onLogInClick={onLogInClick}
          onLogOutClick={onLogOutClick}
          isLoggedIn={isLoggedIn}
          isMobile={false}
          isMobileMenuOpened={isMobileMenuOpened}
        />
        <button
          type="button"
          onClick={onMenuButtonClick}
          className={headerMobileMenuButtonClassName}
        />
      </div>

      {children}

      <div className={headerMobileMenuOverlayClassName} onClick={onOverlayClick}>
        <Menu
          onLogInClick={onLogInClick}
          onLogOutClick={onLogOutClick}
          isLoggedIn={isLoggedIn}
          isMobile={true}
          isMobileMenuOpened={isMobileMenuOpened}
        />
      </div>
    </header>
  );
}

Header.propTypes = {
  isLoggedIn: PropTypes.bool.isRequired,
  onLogInClick: PropTypes.func.isRequired,
  onLogOutClick: PropTypes.func.isRequired,
  children: PropTypes.element,
  onMenuButtonClick: PropTypes.func.isRequired,
  isMobileMenuOpened: PropTypes.bool.isRequired,
  isPopupOpened: PropTypes.bool.isRequired,
  onOverlayClick: PropTypes.func.isRequired,
};

export default Header;
