/**
 * @method addArticleToSavedNews
 * @description –ú–µ—Ç–æ–¥ —Å–æ—Ö—Ä–∞–Ω–µ–Ω–∏—è —Å—Ç–∞—Ç—å–∏ –≤ –ë–î.
 * @param {Object} article - –æ–±—ä–µ–∫—Ç —Å –¥–∞–Ω–Ω—ã–º–∏ —Å—Ç–∞—Ç—å–∏
 * @returns {JSON}
 * @since v. 1.1.0
 */
export const addArticleToSavedNews = (article) => {
  return fetch(`${BASE_URL}${PATH_TO.SAVED_NEWS}`, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
      authorization: `Bearer ${getTokenFromStorage()}`,
    },
    body: JSON.stringify(article),
  }).then((res) => {
    if (res.ok) {
      return res.json();
    }
    return Promise.reject(`–û—à–∏–±–∫–∞ ${res.status}: ${res.statusText}`);
  });
};
