import { forLogin as config } from '../../configs/configForComponents';
import { useEffect } from 'react';
import PopupWithForm from '../PopupWithForm/PopupWuthForm';
import { useFormWithValidation } from '../../hooks/useFormWithValidation';
import PropTypes from 'prop-types';

/**
 * @module Login
 * @description –§—É–Ω–∫—Ü–∏–æ–Ω–∞–ª—å–Ω—ã–π –∫–æ–º–ø–æ–Ω–µ–Ω—Ç<br>
 * –ü–æ–ø–∞–ø, —Ñ–æ—Ä–º–∞ –≤—Ö–æ–¥–∞ (–∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏–∏) –≤ –ø—Ä–∏–ª–æ–∂–µ–Ω–∏–µ.
 * @property {Boolean} isOpened - - —Å—Ç–µ–π—Ç –æ—Ç–∫—Ä—ã—Ç–æ–≥–æ —Å–æ—Å—Ç–æ—è–Ω–∏—è –ø–æ–ø–∞–ø–∞
 * @property {Boolean} isRequestProcessing - —Å—Ç–µ–π—Ç —Å–æ—Å—Ç–æ—è–Ω–∏—è –∑–∞–ø—Ä–æ—Å–∞: true - –≤—ã–ø–æ–ª–Ω—è–µ—Ç—Å—è, false - –Ω–µ –≤—ã–ø–æ–ª–Ω—è–µ—Ç—Å—è
 * @property {Function} onClose - –∫–æ–ª–±—ç–∫, –∑–∞–∫—Ä—ã–≤–∞–µ—Ç –ø–æ–ø–∞–ø—ã –ø—Ä–∏ –∫–ª–∏–∫–µ –ø–æ –∫—Ä–µ—Å—Ç–∏–∫—É
 * @property {Function} onOverlayClick - –∫–æ–ª–±—ç–∫, –∑–∞–∫—Ä—ã–≤–∞–µ—Ç –ø–æ–ø–∞–ø—ã –ø—Ä–∏ –∫–ª–∏–∫–µ –ø–æ –æ–≤–µ—Ä–ª–µ—é
 * @property {Function} onRedirectLinkClick - –∫–æ–ª–±—ç–∫, –ø–µ—Ä–µ–≤–æ–¥–∏—Ç –Ω–∞ –¥—Ä—É–≥—É—é —Å—Ç—Ä–∞–Ω–∏—Ü—É
 * @property {Function} handleLogin - –∫–æ–ª–±—ç–∫, –æ—Ç–ø—Ä–∞–≤–ª—è–µ—Ç –∑–∞–ø—Ä–æ—Å –ø—Ä–∏ —Å–∞–±–º–∏—Ç–µ —Ñ–æ—Ä–º—ã
 * @returns {JSX} - JSX-—Ñ—Ä–∞–≥–º–µ–Ω—Ç —Ä–∞–∑–º–µ—Ç–∫–∏, —Ñ–æ—Ä–º–∞ –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏–∏ –≤ –ø—Ä–∏–ª–æ–∂–µ–Ω–∏–∏
 * @since v.1.0.0
 */
function Login({
  isOpened,
  isRequestProcessing,
  onClose,
  onOverlayClick,
  onRedirectLinkClick,
  handleLogin,
}) {
  const {
    FORM_TITLE,
    SUBMIT_BUTTON_TEXT,
    REDIRECT_TITLE_TEXT,
    REDIRECT_LINK_TEXT,
    EMAIL_LABEL,
    EMAIL_PLACEHOLDER,
    PASSWORD_LABEL,
    PASSWORD_PLACEHOLDER,
  } = config;

  const {
    values,
    errors,
    isFormValid,
    handleInputChange,
    resetForm,
    formError,
    setFormError,
  } = useFormWithValidation();

  const { login, password } = values;

  /**
   * @method handleSubmit
   * @description –ü—É–±–ª–∏—á–Ω—ã–π –º–µ—Ç–æ–¥<br>
   * –û–±—Ä–∞–±–æ—Ç—á–∏–∫ —Å–∞–±–º–∏—Ç–∞ —Ñ–æ—Ä–º—ã –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏–∏. –í–æ–∑–≤—Ä–∞—â–∞–µ—Ç —Ç–æ–∫–µ–Ω.
   * @param {Event} evt - —Å–æ–±—ã—Ç–∏–µ
   * @public
   * @returns {Object} —Ç–æ–∫–µ–Ω –≤ —Ñ–æ—Ä–º–∞—Ç–µ {token: <—Ç–æ–∫–µ–Ω>}
   * @since v.1.1.0
   */
  const handleSubmit = (evt) => {
    evt.preventDefault();
    handleLogin({ email: login, password }, setFormError);
  };

  useEffect(() => {
    resetForm();
    //eslint-disable-next-line
  }, [isOpened]);

  return (
    <PopupWithForm
      formTitle={FORM_TITLE}
      submitButtonText={SUBMIT_BUTTON_TEXT}
      redirectTitleText={REDIRECT_TITLE_TEXT}
      redirectLinkText={REDIRECT_LINK_TEXT}
      isOpened={isOpened}
      onClose={onClose}
      onOverlayClick={onOverlayClick}
      onRedirectLinkClick={onRedirectLinkClick}
      isDisabled={!isFormValid}
      onSubmit={handleSubmit}
      formError={formError}
      isRequestProcessing={isRequestProcessing}
    >
      <>
        <ul className="form__inputs">
          <li className="form__field">
            <label className="form__input-label">{EMAIL_LABEL}</label>
            <input
              id="login"
              name="login"
              type="email"
              onChange={handleInputChange}
              value={login || ''}
              className="form__input"
              placeholder={EMAIL_PLACEHOLDER}
              disabled={isRequestProcessing}
              required
            />
            <span className="form__input-error" id="login-input-error">
              {errors.login || ''}
            </span>
          </li>
          <li className="form__field">
            <label className="form__input-label">{PASSWORD_LABEL}</label>
            <input
              id="password"
              name="password"
              type="password"
              onChange={handleInputChange}
              value={password || ''}
              className="form__input"
              placeholder={PASSWORD_PLACEHOLDER}
              disabled={isRequestProcessing}
              required
            />
            <span className="form__input-error" id="password-input-error">
              {errors.password || ''}
            </span>
          </li>
        </ul>
      </>
    </PopupWithForm>
  );
}

Login.propTypes = {
  isOpened: PropTypes.bool.isRequired,
  isRequestProcessing: PropTypes.bool.isRequired,
  onClose: PropTypes.func.isRequired,
  onOverlayClick: PropTypes.func.isRequired,
  onRedirectLinkClick: PropTypes.func.isRequired,
  handleLogin: PropTypes.func.isRequired,
};

export default Login;
