/**
 * @function pluralize
 * @description –£—Ç–∏–ª–∏—Ç–∞, –ø–æ–∑–≤–æ–ª—è—é—â–∞—è —Å–∫–ª–æ–Ω—è—Ç—å —Å—É—â–µ—Å—Ç–≤–∏—Ç–µ–ª—å–Ω—ã–µ –≤ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏ –æ—Ç –∑–Ω–∞—á–µ–Ω–∏—è —á–∏—Å–ª–∞, —Å –∫–æ—Ç–æ—Ä—ã–º
 *  –æ–Ω–∏ –∏—Å–ø–æ–ª—å–∑—É—é—Ç—Å—è. –° –ø–æ–º–æ—â—å—é —É—Ç–∏–ª–∏—Ç—ã pluralize –≤–æ–∑–º–æ–∂–Ω–æ —Å–æ–∑–¥–∞–≤–∞—Ç—å —Ñ—Ä–∞–≥–º–µ–Ω—Ç—ã —Ñ—Ä–∞–∑, –¥–ª—è –ø–æ—Å–ª–µ–¥—É—é—â–µ–≥–æ –∏—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω–∏—è
 *  –≤ –∑–∞–≥–æ–ª–æ–≤–∫–∞—Ö –∏ –±–ª–æ–∫–∞—Ö —Å –æ–ø–∏—Å–∞–Ω–∏–µ–º.<br>
 * –£—Ç–∏–ª–∏—Ç–∞ –ø—Ä–∏–Ω–∏–º–∞–µ—Ç –¥–≤–∞ –∞—Ä–≥—É–º–µ–Ω—Ç–∞:
 *  - —á–∏—Å–ª–æ,
 *  - –æ–±—ä–µ–∫—Ç —Å –∫–æ–Ω—Ñ–∏–≥—É—Ä–∞—Ü–∏–µ–π.
 * –û–±—ä–µ–∫—Ç —Å –∫–æ–Ω—Ñ–∏–≥—É—Ä–∞—Ü–∏–µ–π —Å–æ–¥–µ—Ä–∂–∏—Ç –∫–æ–Ω–∫—Ä–µ—Ç–Ω—ã–µ —Ñ—Ä–∞–≥–º–µ–Ω—Ç—ã —Ç–µ–∫—Å—Ç–∞, –∫–æ—Ç–æ—Ä—ã–µ –±—É–¥—É—Ç –∏—Å–ø–æ–ª—å–∑–æ–≤–∞—Ç—å—Å—è —Å –ø–µ—Ä–µ–¥–∞–Ω–Ω—ã–º —á–∏—Å–ª–æ–º.<br>
 * –ò—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω–∏–µ —É—Ç–∏–ª–∏—Ç—ã:
 * –£—Ç–∏–ª–∏—Ç—É –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ –∏–º–ø–æ—Ä—Ç–∏—Ä–æ–≤–∞—Ç—å –≤ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç –∏ –≤—ã–∑–≤–∞—Ç—å –µ–µ, –ø–µ—Ä–µ–¥–∞–≤ –Ω–µ–æ–±—Ö–æ–¥–∏–º—ã–µ –∞—Ä–≥—É–º–µ–Ω—Ç—ã.
 * –ö–æ–Ω—Ñ–∏–≥—É—Ä–∞—Ü–∏—è –º–æ–∂–µ—Ç –±—ã—Ç—å —Å–æ–∑–¥–∞–Ω–∞ –ø–æ –º–µ—Å—Ç—É –∏—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω–∏—è —É—Ç–∏–ª–∏—Ç—ã, –∞ –º–æ–∂–µ—Ç –±—ã—Ç—å –∏–º–ø–æ—Ä—Ç–∏—Ä–æ–≤–∞–Ω–∞ –≤ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç –∏–∑
 *  –æ—Ç–¥–µ–ª—å–Ω–æ–≥–æ —Ñ–∞–π–ª–∞ –∫–æ–Ω—Ñ–∏–≥—É—Ä–∞—Ü–∏–∏.
 * @param {Number} n - —á–∏—Å–ª–æ
 * @param {Object} config -  –∫–æ–Ω—Ñ–∏–≥ —É—Ç–∏–ª–∏—Ç—ã
 * @property {String} zero - —Ñ—Ä–∞–≥–º–µ–Ω—Ç —Ç–µ–∫—Å—Ç–∞, –∫–æ–≥–¥–∞ —á–∏—Å–ª–æ —Ä–∞–≤–Ω–æ 0
 * @property {String} one - —Ñ—Ä–∞–≥–º–µ–Ω—Ç —Ç–µ–∫—Ç—Å–∞, –∫–æ–≥–¥–∞ —á–∏—Å–ª–æ —Ä–∞–≤–Ω–æ 1 –∏–ª–∏ –∑–∞–∫–∞–Ω—á–∏–≤–∞–µ—Ç—Å—è –µ–¥–∏–Ω–∏—Ü–µ–π, –Ω–∞–ø—Ä–∏–º–µ—Ä 21, 31, –Ω–æ –Ω–µ 11
 * @property {String} few - —Ñ—Ä–∞–≥–º–µ–Ω—Ç —Ç–µ–∫—Å—Ç–∞, –∫–æ–≥–¥–∞ —á–∏—Å–ª–æ –∑–∞–∫–∞–Ω—á–∏–≤–∞–µ—Ç—Å—è –Ω–∞ —Ü–∏—Ñ—Ä—ã –æ—Ç 2 –¥–æ 4, –Ω–∞–ø—Ä–∏–º–µ—Ä 2, 23, 34
 * @property {String} many - —Ñ—Ä–∞–≥–º–µ–Ω—Ç —Ç–µ–∫—Å—Ç–∞, –∫–æ–≥–¥–∞ —á–∏—Å–ª–æ –∑–∞–Ω—á–∏–≤–∞–µ—Ç—Å—è —Ü–∏—Ñ—Ä–∞–º–∏ 5 - 9, 11-19.
 * @property {Number} radix - –¥–µ–ª–∏—Ç–µ–ª—å, –¥–ª—è –æ—Ç–¥–µ–ª–µ–Ω–∏—è –æ—Ç —á–∏—Å–ª–∞ —á–∞—Å—Ç–∏, –∫—Ä–∞—Ç–Ω–æ–π —ç—Ç–æ–º—É –¥–µ–ª–∏—Ç–µ–ª—é, –ø–æ —É–º–æ–ª—á–∞–Ω–∏—é 10
 * @property {Number} fewMax - –ø–∞—Ä–∞–º–µ—Ç—Ä, —É—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞—é—â–∏–π —Å–º–µ–Ω—É —Å–∫–ª–æ–Ω–µ–Ω–∏—è, –ø–æ —É–º–æ–ª—á–∞–Ω–∏—é 4
 * @public
 * @version 1.0.0
 * @example
 * const config = {
 *   zero: ', —É –í–∞—Å –Ω–µ—Ç —Å–æ—Ö—Ä–∞–Ω–µ–Ω–Ω—ã—Ö —Å—Ç–∞—Ç–µ–π',
 *   one: ', —É –í–∞—Å {} —Å–æ—Ö—Ä–∞–Ω–µ–Ω–Ω–∞—è —Å—Ç–∞—Ç—å—è',
 *   few: ', —É –í–∞—Å {} —Å–æ—Ö—Ä–∞–Ω–µ–Ω–Ω—ã–µ —Å—Ç–∞—Ç—å–∏',
 *   many: ', —É –í–∞—Å {} —Å–æ—Ö—Ä–∞–Ω–µ–Ω–Ω—ã—Ö —Å—Ç–∞—Ç–µ–π',
 * };
 * const phrase = pluralize(10, config);
 * –†–µ–∑—É–ª—å—Ç–∞—Ç:
 * —É –í–∞—Å 10 —Å–æ—Ö—Ä–∞–Ω–µ–Ω–Ω—ã—Ö —Å—Ç–∞—Ç–µ–π
 */
const pluralize = (n, { zero, one, few, many, radix, fewMax }) => {
  const order = n.toString().length; // –ø–æ—Ä—è–¥–æ–∫ —á–∏—Å–ª–∞
  const mod = Math.abs(n) % radix;
  let pattern;

  if (order === 1) {
    // —á–∏—Å–ª–∞ –æ—Ç 0 –¥–æ 9 –≤–∫–ª—é—á–∏—Ç–µ–ª—å–Ω–æ
    if (n === 0) pattern = zero;
    else if (n === 1) pattern = one;
    else if (n > 1 && n <= fewMax) pattern = few;
    else pattern = many;
  } else if (order === 2) {
    // —á–∏—Å–ª–∞ –æ—Ç 10 –¥–æ 99 –≤–∫–ª—é—á–∏—Ç–µ–ª—å–Ω–æ
    const s = Math.floor(n / radix);
    if (s === 1) pattern = many;
    else if (s > 1) {
      if (mod === 0 || mod > fewMax) pattern = many;
      else if (mod > 1 && mod <= fewMax) pattern = few;
      else pattern = one;
    }
  } else {
    // —á–∏—Å–ª–∞ –æ—Ç 100 –∏ —Å–∫–æ–ª—å–∫–æ –¥—É—à–µ —É–≥–æ–¥–Ω–æ
    const stringNumber = n.toString();
    const newN = +stringNumber.slice(stringNumber.length - 2);
    const newS = Math.floor(newN / radix);
    const newMod = Math.abs(newN) % radix;
    if (newS === 1) pattern = many;
    else if (newS > 1) {
      if (newMod === 0 || newMod > fewMax) pattern = many;
      else if (newMod > 1 && newMod <= fewMax) pattern = few;
      else pattern = one;
    }
  }

  return pattern.replace('{}', n);
};

export default pluralize;
